import { Button, makeStyles, tokens } from '@fluentui/react-components';
import { PlayRegular, StopRegular } from '@fluentui/react-icons';
import { useCallback, useState } from 'react';

import GameView from './GameView';

function App() {
  const styles = useStyles();
  const [inGame, setInGame] = useState(false);
  const [inAnimation, setInAnimation] = useState(false);

  function handleStartEndButtonClick() {
    if (!inAnimation) {
      setInGame(!inGame);;
    }
  }

  const handleGameEnd = useCallback(() => {
    // No need to end the game immediately. The user can click the end game button themselves.
  }, []);

  const handleAnimationStart = useCallback(() => {
    setInAnimation(true);
  }, []);

  const handleAnimationEnd = useCallback(() => {
    setInAnimation(false);
  }, []);

  return (
    <div className={styles.app}>
      <div className={styles.appHeader}>
        <Button
          onClick={handleStartEndButtonClick}
          icon={inGame ? <StopRegular /> : <PlayRegular />}
          appearance='primary'
        >
          <span className={styles.startEndButtonText}>
            {inGame ? 'End Game' : 'Start Game'}
          </span>
        </Button>
      </div>
      <GameView
        blackPlayer='human'
        whitePlayer='human'
        inGame={inGame}
        inAnimation={inAnimation}
        onGameEnd={handleGameEnd}
        onAnimationStart={handleAnimationStart}
        onAnimationEnd={handleAnimationEnd}
      />
    </div>
  );
}

export default App;

const useStyles = makeStyles({
  app: {
    height: '100%',
    margin: 0,
    padding: 0,

    display: 'flex',
    flexDirection: 'column',
    alignItems: 'stretch',
  },
  appHeader: {
    display: 'flex',
    justifyContent: 'center',
    padding: `${tokens.spacingVerticalL} ${tokens.spacingHorizontalL} 0`
  },
  startEndButtonText: {
    width: '6em',
    textAlign: 'end',
  },
});
