import * as BoardConstants from './BoardConstants';
import SquareState, { isDiscState, isLegalMoveState } from './SquareState';

/**
 * The visual properties of a square.
 */
type SquareProps = {
  size: number;
  cornerRadius: number;
  opacities: {
    shadow: number;
    blackLegalMove: number;
    whiteLegalMove: number;
    blackDisc: number;
    whiteDisc: number;
  },
};

export default SquareProps;

/**
 * Returns the visual properties of a square in the given state.
 * @param state - The state of the square.
 * @param counterpart - If `state` is the beginning of a transition, `counterpart` is the end of the
 * transition. If `state` is the end of a transition, `counterpart` is the beginning of the
 * transition.
 */
export function getSquareProps(state: SquareState, counterpart: SquareState) {
  const props: SquareProps = {
    size: 0,
    cornerRadius: 0,
    opacities: {
      shadow: 0,
      blackLegalMove: 0,
      whiteLegalMove: 0,
      blackDisc: 0,
      whiteDisc: 0,
    },
  };
  if (isLegalMoveState(state)) {
    props.size = BoardConstants.LEGAL_MOVE_INDICATOR_SIZE;
    if (state === SquareState.BlackLegalMove) {
      props.opacities.blackLegalMove = BoardConstants.LEGAL_MOVE_INDICATOR_OPACITY;
    } else {
      props.opacities.whiteLegalMove = BoardConstants.LEGAL_MOVE_INDICATOR_OPACITY;
    }
  } else if (isDiscState(state)) {
    props.size = BoardConstants.DISC_RADIUS * 2;
    props.cornerRadius = BoardConstants.DISC_RADIUS;
    props.opacities.shadow = 1;
    if (state === SquareState.BlackDisc) {
      props.opacities.blackDisc = 1;
    } else {
      props.opacities.whiteDisc = 1;
    }
  } else if (counterpart !== SquareState.Empty) {
    const counterpartProps = getSquareProps(counterpart, state);
    props.size = counterpartProps.size * 0.5;
    props.cornerRadius = counterpartProps.cornerRadius * 0.5;
  }
  return props;
}
