import {
  Link,
  Toast,
  ToastBody,
  ToastTitle,
  ToastTrigger,
  Toaster,
  useId,
  useToastController,
} from '@fluentui/react-components';
import React, { useCallback, useEffect, useReducer, useRef } from 'react';

import { GameState, Player, PlayerAction, player_opponent } from 'othello-rs';

import Board from './Board';
import PlayerFunction from './PlayerFunction';
import gameViewReducer, {
  getLegalMoveFlatIndices,
  getSquareStatesFromGameState,
} from './gameViewReducer';

/**
 * A component that runs Othello games by taking turns between players and stepping the game state
 * forward.
 */
function GameView({
  blackPlayer,
  whitePlayer,
  inGame,
  inAnimation,
  onGameEnd,
  onAnimationStart,
  onAnimationEnd,
}: GameViewProps) {
  const toasterId = useId('toaster');
  const { dispatchToast } = useToastController(toasterId);

  const resolveBlackPromise = useRef<((action: PlayerAction) => void) | null>(null);
  const resolveWhitePromise = useRef<((action: PlayerAction) => void) | null>(null);
  const dispatchPassToast = useCallback((content: React.ReactNode) => {
    dispatchToast(content, { timeout: 5000, intent: 'info' });
  }, [dispatchToast]);
  const blackPlayerFunction = usePlayerFunction(
    blackPlayer,
    dispatchPassToast,
    resolveBlackPromise,
  );
  const whitePlayerFunction = usePlayerFunction(
    whitePlayer,
    dispatchPassToast,
    resolveWhitePromise,
  );

  const [state, dispatch] = useReducer(gameViewReducer, {
    gameState: null,
    previousSquareStates: getSquareStatesFromGameState(null),
    inAnimation,
    pendingPlayerActionId: null,
    playerAction: null,
  });

  useEffect(() => {
    if (inGame) {
      dispatch({ type: 'gameStart' });
    } else {
      dispatch({ type: 'gameEnd' });
    }
  }, [inGame]);

  useEffect(() => {
    if (state.gameState === null) {
      return;
    }
    if (isEndgame(state.gameState)) {
      dispatchToast(createEndgameToast(state.gameState), { timeout: -1, intent: 'success' });
      onGameEnd();
    } else {
      const playerFunction = state.gameState.current_player === Player.Black ?
        blackPlayerFunction : whitePlayerFunction;
      const playerActionId = maxPlayerActionId;
      ++maxPlayerActionId;
      dispatch({ type: 'playerActionStart', playerActionId });
      playerFunction(state.gameState).then(playerAction => {
        dispatch({ type: 'playerActionEnd', playerActionId, playerAction });
      });
    }
  }, [state.gameState, dispatchToast, onGameEnd, blackPlayerFunction, whitePlayerFunction]);

  function handleClick(row: number, column: number) {
    if (state.gameState === null) {
      return;
    }
    const flatIndex = row * 8 + column;
    if (!getLegalMoveFlatIndices(state.gameState).includes(flatIndex)) {
      return;
    }
    const playerAction = PlayerAction.move_to_flat_index(flatIndex);
    switch (state.gameState.current_player) {
      case Player.Black:
        if (resolveBlackPromise.current) {
          resolveBlackPromise.current(playerAction);
        }
        resolveBlackPromise.current = null;
        break;
      case Player.White:
        if (resolveWhitePromise.current) {
          resolveWhitePromise.current(playerAction);
        }
        resolveWhitePromise.current = null;
        break;
    }
  }

  const handleAnimationStart = useCallback(() => {
    dispatch({ type: 'animationStart' });
    onAnimationStart();
  }, [onAnimationStart]);

  const handleAnimationEnd = useCallback(() => {
    dispatch({ type: 'animationEnd' });
    onAnimationEnd();
  }, [onAnimationEnd]);

  return (
    <>
      <Toaster toasterId={toasterId} />
      <Board
        fromSquareStates={state.previousSquareStates}
        toSquareStates={getSquareStatesFromGameState(state.gameState)}
        inAnimation={inAnimation}
        onClick={handleClick}
        onAnimationStart={handleAnimationStart}
        onAnimationEnd={handleAnimationEnd}
      />
    </>
  );
}

export default GameView;

export type GameViewProps = {
  blackPlayer: PlayerFunction | 'human';
  whitePlayer: PlayerFunction | 'human';
  inGame: boolean;
  inAnimation: boolean;
  onGameEnd: () => void;
  onAnimationStart: () => void;
  onAnimationEnd: () => void;
};

let maxPlayerActionId = 0;

function getPlayerName(player: Player) {
  switch (player) {
    case Player.Black:
      return 'Black';
    case Player.White:
      return 'White';
  }
}

/**
 * Returns whether the given game state is an endgame.
 *
 * In othello-rs, the endgame is the state where both players have passed their turns. This
 * definition is not suitable for interactive games, so we look one step ahead to determine if the
 * game should end.
 */
function isEndgame(gameState: GameState) {
  if (gameState.is_endgame) {
    return true;
  }
  const actions = gameState.actions_vec();
  return actions[0].is_pass() && gameState.apply_action(actions[0]).is_endgame;
}

/**
 * Wraps a player function to handle the case where the player is a human or the player runs out of
 * moves.
 */
function usePlayerFunction(
  player: PlayerFunction | 'human',
  dispatchPassToast: (content: React.ReactNode) => void,
  resolvePromise: React.MutableRefObject<((action: PlayerAction) => void) | null>,
): PlayerFunction {
  return useCallback((gameState: GameState) => {
    const actions = gameState.actions_vec();
    if (actions[0].is_pass()) {
      const playerName = getPlayerName(gameState.current_player);
      const opponentName = getPlayerName(player_opponent(gameState.current_player));
      dispatchPassToast(
        <Toast>
          <ToastTitle action={<ToastTrigger><Link>Dismiss</Link></ToastTrigger>}>
            {`${playerName} passed`}
          </ToastTitle>
          <ToastBody>
            {`It is ${opponentName}'s turn because ${playerName} ran out of moves.`}
          </ToastBody>
        </Toast>
      );
      return new Promise(resolve => {
        setTimeout(() => {
          resolve(actions[0]);
        }, 1000);
      });
    } else if (player === 'human') {
      return new Promise<PlayerAction>(resolve => {
        resolvePromise.current = resolve;
      });
    } else {
      return player(gameState);
    }
  }, [player, resolvePromise, dispatchPassToast]);
}

function createEndgameToast(gameState: GameState) {
  let blackCount = 0;
  let whiteCount = 0;
  for (let flatIndex = 0; flatIndex < 8 * 8; ++flatIndex) {
    switch (gameState.get_disc_at_flat_index(flatIndex)) {
      case Player.Black:
        ++blackCount;
        break;
      case Player.White:
        ++whiteCount;
        break;
    }
  }
  let titleText;
  let bodyText;
  if (blackCount === whiteCount) {
    titleText = 'It was a draw';
    bodyText = `The game was a draw because both players have ${blackCount} discs.`;
  } else {
    const winnerName = getPlayerName(blackCount > whiteCount ? Player.Black : Player.White);
    titleText = `${winnerName} won`;
    bodyText = (
      `${winnerName} won because there were ` +
      `${blackCount} black discs and ${whiteCount} white discs.`
    );
  }
  return (
    <Toast>
      <ToastTitle action={<ToastTrigger><Link>Dismiss</Link></ToastTrigger>}>
        {titleText}
      </ToastTitle>
      <ToastBody>{bodyText}</ToastBody>
    </Toast>
  );
}
