import { GameState, Player, PlayerAction } from 'othello-rs';

import SquareState from './SquareState';

/**
 * A reducer for the state of the `GameView` component.
 */
function gameViewReducer(state: GameViewState, action: GameViewAction): GameViewState {
  switch (action.type) {
    case 'gameStart':
      return {
        gameState: GameState.initial_state(),
        previousSquareStates: getSquareStatesFromGameState(state.gameState),
        inAnimation: state.inAnimation,
        pendingPlayerActionId: null,
        playerAction: null,
      };
    case 'gameEnd':
      return {
        gameState: null,
        previousSquareStates: getSquareStatesFromGameState(state.gameState),
        inAnimation: state.inAnimation,
        pendingPlayerActionId: null,
        playerAction: null,
      };
    case 'animationStart':
      return {
        ...state,
        inAnimation: true,
      };
    case 'animationEnd':
      return applyPlayerActionIfPossible({
        ...state,
        inAnimation: false,
      });
    case 'playerActionStart':
      if (!state.gameState) {
        return state;
      }
      return {
        ...state,
        pendingPlayerActionId: action.playerActionId,
        playerAction: null,
      };
    case 'playerActionEnd':
      if (state.pendingPlayerActionId !== action.playerActionId) {
        return state;
      }
      return applyPlayerActionIfPossible({
        ...state,
        pendingPlayerActionId: null,
        playerAction: action.playerAction,
      });
    default:
      console.error(`GameViewAction of an invalid type: ${action}`);
      return state;
  }
}

export default gameViewReducer;

export type GameViewState = {
  gameState: GameState | null;
  previousSquareStates: SquareState[][];
  inAnimation: boolean;
  pendingPlayerActionId: number | null;
  playerAction: PlayerAction | null;
};

export type GameViewAction = (
  { type: 'gameStart' } |
  { type: 'gameEnd' } |
  { type: 'animationStart' } |
  { type: 'animationEnd' } |
  { type: 'playerActionStart', playerActionId: number } |
  { type: 'playerActionEnd', playerActionId: number, playerAction: PlayerAction }
);

export function getSquareStatesFromGameState(gameState: GameState | null) {
  const squareStates = Array.from<undefined, SquareState[]>(
    { length: 8 },
    () => Array(8).fill(SquareState.Empty),
  );
  if (!gameState) {
    return squareStates;
  }
  const legalMoveFlatIndices = getLegalMoveFlatIndices(gameState);
  const legalMoveState = gameState.current_player === Player.Black ?
    SquareState.BlackLegalMove : SquareState.WhiteLegalMove;
  for (let row = 0; row < 8; ++row) {
    for (let column = 0; column < 8; ++column) {
      const flatIndex = row * 8 + column;
      if (legalMoveFlatIndices.includes(flatIndex)) {
        squareStates[row][column] = legalMoveState;
      } else {
        switch (gameState.get_disc_at_flat_index(flatIndex)) {
          case Player.Black:
            squareStates[row][column] = SquareState.BlackDisc;
            break;
          case Player.White:
            squareStates[row][column] = SquareState.WhiteDisc;
            break;
        }
      }
    }
  }
  return squareStates;
}

export function getLegalMoveFlatIndices(gameState: GameState) {
  const indices = [];
  for (const action of gameState.actions_vec()) {
    if (!action.is_pass()) {
      indices.push(action.flat_index());
    }
  }
  return indices;
}

function applyPlayerActionIfPossible(state: GameViewState): GameViewState {
  // Apply the player action after the animation ends and the player action is available.
  if (state.inAnimation || state.playerAction === null) {
    return state;
  }
  return {
    gameState: state.gameState!.apply_action(state.playerAction),
    previousSquareStates: getSquareStatesFromGameState(state.gameState),
    inAnimation: false,
    pendingPlayerActionId: null,
    playerAction: null,
  };
}
