import React, { useEffect, useMemo, useRef } from 'react';

import * as BoardConstants from './BoardConstants';
import SquareState from './SquareState';
import createSquareDef from './createSquareDef';

import './Board.css';

/**
 * A controlled component that renders an Othello board transitioning between two states.
 */
function Board({
  fromSquareStates,
  toSquareStates,
  inAnimation,
  onClick,
  onAnimationStart,
  onAnimationEnd,
}: BoardProps) {
  const [svgKey, setSvgKey] = React.useState(0);
  const ref = useRef<SVGSVGElement>(null);
  const viewBoxSize = BoardConstants.SQUARE_SIZE * 8 + BoardConstants.MARGIN_WIDTH * 2;

  function handleClick(event: React.MouseEvent<SVGSVGElement>) {
    if (inAnimation) {
      return;
    }
    const screenCTM = ref.current?.getScreenCTM();
    if (!screenCTM) {
      console.error('Failed to get screen CTM');
      return;
    }
    const point = new DOMPoint(event.clientX, event.clientY).matrixTransform(screenCTM.inverse());
    const row = Math.floor((point.y - BoardConstants.MARGIN_WIDTH) / BoardConstants.SQUARE_SIZE);
    const column = Math.floor((point.x - BoardConstants.MARGIN_WIDTH) / BoardConstants.SQUARE_SIZE);
    if (0 <= row && row < 8 && 0 <= column && column < 8) {
      onClick(row, column);
    }
  }

  const fromSquareStatesString = JSON.stringify(fromSquareStates);
  const toSquareStatesString = JSON.stringify(toSquareStates);
  useEffect(() => {
    // Increment the key to force the SVG element to re-render. This is necessary in Safari to make
    // the animations play again.
    setSvgKey(key => key + 1);
    ref.current?.pauseAnimations();
    onAnimationStart();
    ref.current?.setCurrentTime(0);
    ref.current?.unpauseAnimations();
    const timeoutID = setTimeout(
      onAnimationEnd,
      BoardConstants.ANIMATION_DURATION_IN_SECONDS * 1000,
    );
    return () => {
      clearTimeout(timeoutID);
    };
  }, [fromSquareStatesString, toSquareStatesString, onAnimationStart, onAnimationEnd]);

  return (
    <div className='board-svg-container'>
      <svg
        key={svgKey}
        ref={ref}
        className='board-svg'
        viewBox={`0 0 ${viewBoxSize} ${viewBoxSize}`}
        onClick={handleClick}
      >
        {useMemo(() => (
          <>
            {createDefs()}
            {createBorder()}
            {createBackground()}
            {createGrid()}
            {createDots()}
          </>
        ), [])}
        {createSquareUses(fromSquareStates, toSquareStates)}
      </svg>
    </div>
  );
}

export default Board;

export type BoardProps = {
  fromSquareStates: SquareState[][],
  toSquareStates: SquareState[][],
  inAnimation: boolean,
  onClick: (row: number, column: number) => void,
  onAnimationStart: () => void,
  onAnimationEnd: () => void,
};

function getSquareDefId(fromState: SquareState, toState: SquareState) {
  let result = `square-state-${SquareState[fromState]}`;
  if (toState !== fromState) {
    result += `-to-${SquareState[toState]}`;
  }
  return result;
}

function createDefs() {
  const squareStates = Object.keys(SquareState).map(Number).filter(state => !isNaN(state));
  const squareDefs = [];
  for (const fromState of squareStates) {
    for (const toState of squareStates) {
      const id = getSquareDefId(fromState, toState);
      squareDefs.push(createSquareDef(id, fromState, toState));
    }
  }
  return (
    <defs>
      <filter id={BoardConstants.BORDER_SHADOW_FILTER_ID}>
        <feDropShadow dx='0' dy='0' stdDeviation='0.7' />
      </filter>
      <filter id={BoardConstants.DISC_SHADOW_FILTER_ID}>
        <feDropShadow dx='0' dy='0' stdDeviation='0.3' />
      </filter>
      <radialGradient id={BoardConstants.BLACK_DISC_GRADIENT_ID}>
        <stop offset='90%' stopColor='rgb(24, 24, 24)' />
        <stop offset='100%' stopColor='rgb(0, 0, 0)' />
      </radialGradient>
      <radialGradient id={BoardConstants.WHITE_DISC_GRADIENT_ID}>
        <stop offset='90%' stopColor='rgb(255, 255, 255)' />
        <stop offset='100%' stopColor='rgb(192, 192, 192)' />
      </radialGradient>
      {squareDefs}
    </defs>
  );
}

function createBorder() {
  const xy = BoardConstants.MARGIN_WIDTH - BoardConstants.BORDER_WIDTH;
  const size = BoardConstants.SQUARE_SIZE * 8 + BoardConstants.BORDER_WIDTH * 2;
  const radius = BoardConstants.BORDER_WIDTH;
  const commonProps = { x: xy, y: xy, width: size, height: size, rx: radius, ry: radius };
  return (
    <>
      <rect {...commonProps} filter={`url(#${BoardConstants.BORDER_SHADOW_FILTER_ID})`} />
      <rect {...commonProps} />
    </>
  );
}

function createBackground() {
  const xy = BoardConstants.MARGIN_WIDTH;
  const size = BoardConstants.SQUARE_SIZE * 8;
  return (
    <rect x={xy} y={xy} width={size} height={size} fill='rgb(63, 142, 106)' />
  );
}

function createGrid() {
  const lines = [];
  for (let i = 0; i < 9; ++i) {
    const a = BoardConstants.MARGIN_WIDTH + i * BoardConstants.SQUARE_SIZE;
    const b1 = BoardConstants.MARGIN_WIDTH;
    const b2 = BoardConstants.MARGIN_WIDTH + 8 * BoardConstants.SQUARE_SIZE;
    lines.push(
      <line key={`horizontal-${i}`} x1={b1} y1={a} x2={b2} y2={a} />
    );
    lines.push(
      <line key={`vertical-${i}`} x1={a} y1={b1} x2={a} y2={b2} />
    );
  }
  return (
    <g stroke='black' strokeWidth={BoardConstants.GRID_LINE_WIDTH}>
      {lines}
    </g>
  );
}

function createDots() {
  const dots = [];
  for (const row of [2, 6]) {
    const cy = BoardConstants.MARGIN_WIDTH + row * BoardConstants.SQUARE_SIZE;
    for (const column of [2, 6]) {
      const cx = BoardConstants.MARGIN_WIDTH + column * BoardConstants.SQUARE_SIZE;
      dots.push(
        <circle key={`dot-${row}-${column}`} cx={cx} cy={cy} r={BoardConstants.DOT_RADIUS} />
      );
    }
  }
  return (
    <>{dots}</>
  );
}

function createSquareUses(fromStates: SquareState[][], toStates: SquareState[][]) {
  const squareUses = [];
  for (let row = 0; row < 8; ++row) {
    const cy = BoardConstants.MARGIN_WIDTH + (row + 0.5) * BoardConstants.SQUARE_SIZE;
    for (let column = 0; column < 8; ++column) {
      const cx = BoardConstants.MARGIN_WIDTH + (column + 0.5) * BoardConstants.SQUARE_SIZE;
      squareUses.push(
        <use
          key={`square-${row}-${column}`}
          href={`#${getSquareDefId(fromStates[row][column], toStates[row][column])}`}
          transform={`translate(${cx}, ${cy})`}
        />
      );
    }
  }
  return (
    <>{squareUses}</>
  );
}
